<!--  -->
<template>
  <div class="detail-shop-info" v-if="shop">
    <div class="shop-logo">
      <div><img :src="shop.logo" alt="" /></div>
      <span>{{shop.name}}</span>
    </div>
    <div class="shop-des">
      <div class="left">
        <div><span>{{shop.sells|approximate}}</span><br><span>总销量</span></div>
        <div><span>{{shop.goodsCount}}</span><br><span>全部宝贝</span></div>
      </div>
      <div class="right">
        <p v-for="(item,index) in shop.score" :key="index">
          <span>{{item.name}}</span>
          <span :class="{turnred:item.score>=5}">{{item.score}}</span>
          <span :class="{turnred:item.score>=5}">{{item.score>=5? "\t高":"低"}}</span>
        </p>
      </div>
    </div>
    <div class="button">
      <button>进店逛逛</button>
    </div>

  </div>
</template>

<script>
export default {
  name: "DetailShopInfo",
  data() {
    return {};
  },
  props: {
    shop: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  filters:{
    approximate(val){
       return val>10000? (val/10000).toFixed(2)+"万":val
    }
  }
};
</script>
<style scoped>
.detail-shop-info {
  padding: 20px 10px 20px 5px;
  border-bottom: 5px solid #ece2e2;
}
.shop-logo {
  display: flex;
}
.shop-logo > div {
  width: 50px;
  height: 50px;
  border: 1px solid rgb(235, 225, 225);
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shop-logo div img {
  width: 110%;
  height: 110%;
}
.shop-logo>span{
   margin-left: 10px;
   line-height: 2.5em;
   font-size: 20px;
}
.shop-des{
  display: flex;
}
.left{
  display: flex;
  align-items: center;
  margin-top:16px;
  flex:1;
  border-right: 1px solid rgb(196, 192, 192);
}
.left>div{
  font-size: 18px;
  text-align:center;
}
.left>div>span{
  /* 强制不换行 */
  white-space:nowrap;   
}
.left>div:nth-child(1){
  margin: 0px 20px 0px 20px;

}
.right{
  flex:1;
  font-size:18px;
  margin-left:20px
}
.right>p>span{
  margin-left:10px;
}
.right>p>span:nth-child(2){
  color:green;
}
.right>p>span:nth-child(3){
  color:green;
}
.turnred{
  color:red !important;
}
.button{
  margin-top:20px;
  width: 100%;
  height: 30px;
  text-align: center;
}
.button>button{
  background: #f3f4fa;
  border: 1px solid #f3f4fa;
  color:#8a8b92;
  border-radius: 10px;
  outline-style: none;
  width:40%;
  height: 30px;
}
</style>