<!--  -->
<template>
  <div v-if="detailInfo&&detailInfo.detailImage&&detailInfo.detailImage.length">
     <div class="desc">
         <div>{{detailInfo.desc}}</div>
     </div>
     <div class="image">
         <div v-for="(item,index) in detailInfo.detailImage" :key="index">
             <p class="summary">{{item.key}}</p>
            <img v-for="(item1,index1) in item.list" :key="index1" :src="item1" alt="" @load="loadingComplete()">
         </div>
     </div>
  </div>
</template>

<script>
export default {
 name:'',
  data () {
    return {
        count:0,
        imagesLength:0
    };
  },
  props:{
      detailInfo:{
          type:Object,
          default(){
              return {}
          }
      }
  },
  methods: {
      loadingComplete(){
          if(this.count++>= this.imagesLength){
               this.$emit("loadingcomplete")
          }
        
      }
  },
  watch:{
      detailInfo(){
          this.imagesLength = this.detailInfo.detailImage[0].list.length 
      }
  }
}
</script>
<style scoped>
.desc{  
    margin:20px 0px 0px 20px;
    padding:20px 0px 20px 0px ;
    position: relative;
    width:90%;
    overflow: hidden;

}
.desc>div{
    margin-top:20px ;
}
.image>div>img{
    width:100%;
}
.desc::before{
    content:"\2605";
    position: absolute;
    top:0;
    left:0;
    width:25%;
    height: 14px;
    display: inline-block;
    border-bottom:1px solid #646060;
}
.desc::after{
    content:"\2605";
    position: absolute;
    bottom: 0;
    right: 0;
    width:25%;
    height: 14px;
    display: flex;
    flex-direction: row-reverse;
    border-bottom:1px solid #646060;
}
.summary{
    margin:10px 20px 10px 20px;
}
</style>