<!--  -->
<template>
  <div v-if="rate">
      <div class="commentBar">
          <span>用户评价</span>
          <span>更多</span>
      </div>
      <div>
          <div class="user">
               <div><img :src="rate.list[0].user.avatar"></div>
               <span>{{rate.list[0].user.uname}}</span>
          </div>
          <p class="content">{{rate.list[0].content}}</p>  
        <div class="extraInfo"><span>{{rate.list[0].created|dateChange}}</span><span>{{rate.list[0].style}}</span></div>    
      </div>
      <div v-if="rate.list[0].images" class="showImage">
          <img v-for="(item,index) in rate.list[0].images" :key="index" alt="" :src="item">
      </div>
  </div>
</template>

<script>
import {formatDate} from "common/util.js"
export default {
 name:'',
  data () {
    return {
    };
  },
  props:{
      rate:{
          type:Object,
          default(){
              return {}
          }
      }
  },
  filters:{
      dateChange(val){
          return formatDate(new Date(val*1000),"yyyy-MM-dd")+" "
      }
  }
}
</script>
<style scoped>
.commentBar{
    height: 40px;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin:0px 20px;
    padding: 10px 0px;
    font-size: 18px;
    border-bottom:1px solid rgb(187, 177, 177) ;
}
.user{
    width: 90%;
    display: flex;
    align-items: center;
    margin-top:20px;
}
.user>div{
    width:50px;
    height: 50px;
    margin:0px 10px 0px 10px ;
    border:1px solid red;
    border-radius: 50%;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.user>div>img{
    width: 90%;
    height: 90%;
}
.content{
    width: 90%;
    margin:20px 10px 20px 20px;
    color:rgb(66, 62, 62);
    line-height: 1.5em;
}
.extraInfo{
    margin:20px 10px 20px 20px;
    color:rgb(124, 119, 119)
}
.showImage{
    width:100%;
    display: flex;
    overflow: hidden;
    margin-left:25px;
}
.showImage>img{
    margin-right: 5px;
    width:70px;
    height: 70px;
}
</style>