<!--  -->
<template>
  <div>
    <nav-bar>
      <template v-slot:left>
        <div class="left" @click="back()">
          <img src="~assets/img/detail/leftArrow.svg" />
        </div>
      </template>
      <template v-slot:middle>
        <div class="middle">
          <div v-for="(item, index) in title" :key="index" :class="{turnRed:currentIndex===index}" @click="isActived(index)">{{item}}</div>
        </div>
      </template>
    </nav-bar>
  </div>
</template>

<script>
import NavBar from "components/common/NavBar/NavBar.vue";
export default {
  name: "DetailNavBar",
  data() {
    return {
      title: ["商品","参数","评论","推荐"],
      currentIndex:0
    };
  },
  components: {
    NavBar,
  },
  methods: {
      isActived(index){     // 控制导航栏的活跃颜色
          this.currentIndex = index 
          this.$emit("navBarClick",index)
      },
      back(){
          this.$router.back()
      }
  },
};
</script>
<style scoped>
.left{
    width:30%;
    height: 100%;
    margin-top:5px;
    margin-left:10px;
}
.left>img{
    width: 100%;
    height: 100%;

}
.middle{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.middle div{
    font-size:20px;
    color:#667478;
}
.middle .turnRed{  
    color:red;
}
</style>