<style scoped>
.table{
    width: 100%;    
    /* 可以使tr设置border属性并将表格的边框合并到单一的边框 */
    border-collapse:collapse;
    margin:20px;
    color:#909090;
}
.table>tr{
    border-bottom: 1px solid rgb(211, 205, 205);
