<!--  -->
<template>
  <div class="good-detail">
    <detail-nav-bar @navBarClick="reachHere($event)" ref="navbar"></detail-nav-bar>
    <scroll class="content" ref="scroll" @scroll="changeColorByPosition($event)">
      <detail-swiper :topImages="topImages"></detail-swiper>
      <detail-base-info :good="good"></detail-base-info>
      <detail-shop-info :shop="shop"></detail-shop-info>
      <detail-good-info
        :detailInfo="detailInfo"
        @loadingcomplete="loadingComplete()"
        ref="detailGoodInfo"
      ></detail-good-info>
      <detail-param-info
        :itemParams="itemParams"
        ref="params"
      ></detail-param-info>
      <detail-comment-info :rate="rate" ref="comment"></detail-comment-info>
      <goods-list :lists="recommendGood" ref="recommend"></goods-list>
    </scroll>
    <detail-bottom-bar @addshoppingcar="addShoppingCar()"></detail-bottom-bar>
    <back-top
      class="moveto"
      @click.native="backTotop()"
      v-show="isshow"
    ></back-top>
  </div>
</template>
<script>
import DetailNavBar from "./childComponent/DetailNavBar.vue";
import DetailSwiper from "./childComponent/DetailSwiper.vue";
import DetailBaseInfo from "./childComponent/DetailBaseInfo.vue";
import DetailShopInfo from "./childComponent/DetailShopInfo.vue";
import DetailGoodInfo from "./childComponent/DetailGoodInfo";
import DetailParamInfo from "./childComponent/DetailParamInfo";
import DetailCommentInfo from "./childComponent/DetailCommentInfo.vue";
import DetailBottomBar from "./childComponent/DetailBottomBar"

import {
  getDetailData,
  Goods,
  Shop,
  getRecommendData,
} from "network/detail.js";

import { itemListenerMixin,backTopMixin } from "common/mixin.js";

import {debouce} from "common/util.js"

import Scroll from "components/common/Scroll/Scroll.vue";
import GoodsList from "components/content/Goods/GoodsList.vue";



export default {
  name: "GoodDetail", // name属性值可以在keep-alive 中用于排除 组件
  data() {
    return {
      iid: 0,
      topImages: [],
      good: null,
      shop: null,
      detailInfo: null,
      itemParams: null,
      rate: null,
      paramOffSetTop: 0,
      recommendGood: null,
      ItemEvent: null,
      themeTopYs: [],
      getThemeTopY: null,
    };
  },
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    Scroll,
    DetailGoodInfo,
    DetailParamInfo,
    DetailCommentInfo,
    GoodsList,
    DetailBottomBar,
  },
  methods: {
    loadingComplete() {
      console.log("ccccc")
      this.$refs.scroll.refresh()
      this.getThemeTopY()
    },
    reachHere(index) {
      console.log(index);
      this.$refs.scroll.scrollTo(0, -this.themeTopYs[index], 200);
      console.log(this.themeTopYs);
    },
    changeColorByPosition(position){
      let y =-position.y
      this.isshow = position.y <= -1000;
      let currentIndex = 0
      for(let i =0,length=this.themeTopYs.length;i<length;i++){
        if(this.currentIndex !== i&&y>this.themeTopYs[i]&&this.themeTopYs[i+1]){
          this.currentIndex = i
          this.$refs.navbar.currentIndex = this.currentIndex
        }
      }
   
    },
    addShoppingCar()
    {
        const product = {}
        product.image = this.topImages[0]
        product.title = this.good.title
        product.desc = this.good.desc
        product.price = this.good.newPrice
        product.count = 1
        product.iid = this.iid
        product.checked = true
        this.$store.dispatch("addcar",product)    // 添加到Vuex中
    }
 
  },
  created() {
    // 保存传入的iid
    this.iid = this.$route.params.id;
    // 根据iid发送网络请求
    getDetailData(this.iid)
      .then((res) => {
        this.topImages = res.result.itemInfo.topImages;
        this.good = new Goods(
          res.result.itemInfo,
          res.result.columns,
          res.result.shopInfo.services
        );
        this.shop = new Shop(res.result.shopInfo);
        this.detailInfo = res.result.detailInfo;
        this.itemParams = res.result.itemParams;
        this.rate = res.result.rate;

        console.log(res.result);
      })
      .catch((erro) => {
        console.log(erro);
      });
    getRecommendData()
      .then((res) => {
        this.$refs.scroll.refresh();
        this.recommendGood = res.data.list;
        console.log(this.recommendGood);
      })
      .catch((erro) => {
        console.log(erro);
      });
          
      this.getThemeTopY = debouce(()=>{
      this.themeTopYs = []
      this.themeTopYs.push(0)
      this.themeTopYs.push(this.$refs.params.$el.offsetTop)
      this.themeTopYs.push(this.$refs.comment.$el.offsetTop)
      this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
      this.themeTopYs.push(Number.MAX_VALUE)
      console.log("---");
      },100) 
  },
  mounted() {},
  mixins: [itemListenerMixin,backTopMixin],
  destroyed() {
    this.$bus.$off("finishloading", this.ItemEvent); // 移除this.ItemEvent这个回调函数的事件
  },
};
</script>
<style scoped>
.good-detail {
  height: 100vh;
  width: 100wh;
  position: relative;
  z-index: 99;
  background: white;

  /* overflow: hidden; */
}
.content {
  position: absolute;
  width: 100%;
  top: 54px;
  bottom: 0px;
  overflow: hidden;
}
.moveto {
  position: fixed;
  bottom: 100px;
  right: 10px;
}
</style>